.page-onepage {

    .wrapper {

        &__inner {
            z-index: initial;
        }
    } //.page-onepage .wrapper

    .header {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: z(fixed);

        &__inner {
            border: 0;
        }

        &__logo {
            margin-top: -4px;
        }
    } //.page-onepage .header

    .nav {

        &__link {
            line-height: 50px;
        }
    }

    .nav-dropdown {
        top: 50px;
    }

    &--home {

        .onepage {
            @include respond-to(sm) {
                background-color: #fff;
            }
        }

        .logo {

            &__link {
                color: #fff;
            }
        }

        .toggle-mnu {

            span {
                background-color: #fff;

                &::before,
                &::after {
                    background-color: #fff;
                }
            }
        }

        .page-title {
            @include respond-to(sm) {
                width: 100%;
                margin: 0;
                font-size: 16px;
                text-align: center;
            }

            &__wrap {
                @include respond-to(sm) {
                    margin-bottom: 0;
                    padding: $gutter 0;
                    background-color: $color-bg--lighten;
                }
            }
        } //.onepage--home .page-title

        .footer {
            @include respond-to(sm) {
                background-color: $color-bg--lighten;
            }
        }
    } //.onepage--home

    &--about,
    &--reviews {

        .onepage {
            padding-top: 50px;
        }

        @include respond-to(sm) {

            .card-info {

                &__user {
                    @include respond-to(sm) {
                        display: none;
                    }
                }

                &__social {
                    @include respond-to(sm) {
                        display: none;
                    }
                }
            }
        }
    }

    &--about {
        @include respond-to(sm) {

            .page-title {
                text-align: left;

                &__wrap {
                    margin: 0 0 15px;
                    padding: 0;
                    background-color: transparent;
                    align-items: flex-start;
                }
            }
        }
    } //.onepage--about

    &--registration {

        .screen {

            .container {
                @include respond-to(xs) {
                    padding: 0 20px;
                }
            }
        }

        .form {
            padding: 0;

            @include respond-min(xs) {
                padding: 15px 30px 30px;
            }
        }

        .footer {
            padding: 0;
        }
    } //.onepage--registratio
} //.onepage

.onepage {

    &__gallary {
        margin-bottom: $gutter;

        @include respond-to(sm) {

            .bb-slider {

                &__arrow {
                    display: none !important;
                }
            }

            .slick-slide {
                margin: 0;
            }
        }
    } //.onepage__gallary

    &__social {
        display: none;

        @include respond-to(xs) {
            display: block;
        }
    } //.onepage__social

    .page-title {
        @include respond-to(sm) {
            margin: 0;
            font-size: 16px;
        }
    } //.onepage .page-title

    .user {
        @include flexCenter;

        @include respond-to(sm) {
            margin-bottom: 0;
            padding-bottom: 0;
            border: 0;
        }

        &__avatar {
            margin-right: 15px;
            margin-bottom: 0;
        } //.onepage .user__avatar

        &__img {
            width: 50px;
            height: 50px;
        }

        &__name {
            font-family: $f-medium;
            font-size: 16px;
            line-height: 1.2;
            color: $gray--darkest;
            @include respond-to(xs) {
                font-size: 15px;
            }
        }
    } //.onepage .user

    .card {

        &__content {
            margin-top: 25px;
            background-color: transparent;

            @include respond-to(sm) {
                background-color: $color-bg--lighten;
            }
        }

        &__about {
            @include respond-to(sm) {
                margin-bottom: $gutter;
                padding: 0;
                box-shadow: none;
                background-color: transparent;
            }
        }

        &__about_text {
            padding: 10px 15px;
            @extend %box;
        }

        &-services {
            @include respond-to(sm) {
                margin-bottom: 1px;
            }
        }
    } //.onepage .card

    .card-info {
        top: 0;

        &__top {
            padding: 0;
            border-radius: 0;
            background-color: transparent;
            box-shadow: none;

            @include respond-to(xs) {
                display: none;
            }
        } //.onepage .card-info__top

        &__bg {
            margin: 2px;
            padding: 15px;
            @extend %box;
            @include respond-to(sm) {
                padding: 0;
                margin: 0;
                box-shadow: none;
            }
        } //.onepage .card-info__bg

        &__category {
            margin-bottom: 13px;
            padding-top: $gutter;
            border-top: 1px solid $color-border--lightest;

            @include respond-to(sm) {
                border-top: 0;
            }
        } //.onepage .card-info__category

        &__social {
            width: 100%;
            margin: auto;
        } //.onepage .card-info__social
    } //.onepage .card-info

    .card-adress {
        @include respond-to(sm) {

            &__inner {
                padding: 0;
                box-shadow: $bxs-box;
                background-color: #fff;

                .place-text {

                    &__wrap {
                        padding: 13px 15px 10px;

                        & + .place-text__wrap {
                            border-top: 1px solid $color-border--lightest;
                        }
                    }

                    &__box {
                        margin-bottom: 0;
                    }
                }
            }

            .working__list {
                box-shadow: $bxs-box;
                border: 0;
            }
        }

        &__working,
        &__place {
            @include respond-to(sm) {
                margin-bottom: $gutter;
                padding: 0;
                box-shadow: none;
                background-color: transparent;
            }
        }
    } //.onepage .card-adress__working

    .card-about {
        background-color: transparent;
    } //.onepage .card-about

    .card-request-form {

        &__top {
            margin-top: 0;
        }
    } //.onepage .card-request-form

    .social {

        &__inner {
            @include flexCenter;
            justify-content: center;
        }

        &__devider {
            max-width: 100px;
            margin: $gutter / 1.5 auto;
        }

        .bb-title--devider {
            width: 100%;
            margin: 30px auto;

            @include respond-to(sm) {
                margin: 20px 0;
            }

            &::before,
            &::after {
                width: 38%;
            }
        } //.onepage .social .bb-title--devider
    } //.onepage .social

    .button-social,
    .btn-social {
        padding: 10px;

        @include respond-to(sm) {

            &::before {
                font-size: 20px;
            }

            &-text {
                padding: 10px 0;
            }
        }

        & + .button-social {
            margin-left: $gutter / 2;
        }

        & + .btn-social {
            margin-left: $gutter / 2;
        }
    }

    .bb-accordeon {

        &__item,
        &__title {
            @include respond-to(sm) {
                border-radius: 0;
                box-shadow: none;
            }
        }
    } //.onepage .bb-accordeon
} //.onepage

.icon-list {
    margin-left: 0;
    @include row-flex;

    @include respond-min(xs) {
        display: flex;
        justify-content: center;
    }

    .icon-list__item {
        @include col;
        @include size(4);
        @include size-sm(6);
        @include size-xs(12);
    }

    &__item {
        margin-bottom: 30px;
        position: relative;
        @include flexCenter;

        @include respond-min(xs) {
            margin-bottom: 60px;
            flex-direction: column;
        }
    } //.icon-list__item

    &__icon {
        width: 50px;
        height: 50px;
        margin-right: 20px;
        border: 2px solid #fff;
        border-radius: 50%;
        @include flexCenter;

        @include respond-min(xs) {
            width: 70px;
            height: 70px;
            margin-bottom: 20px;
            margin-right: 0;
            display: flex;
            justify-content: center;
        }

        @include respond-min(md) {
            margin-bottom: 20px;
        }

        i {
            margin: auto;
            font-size: 20px;

            @include respond-min(sm) {
                font-size: 30px;
            }
        }
    } //.icon-list__icon

    &__title {
        font-family: $f-medium;
        font-size: 20px;

        @include respond-min(xs) {
            text-align: center;
            font-size: 25px;
        }

        @include respond-min(sm) {
            font-size: 30px;
        }

        @include respond-min(md) {
            font-size: 35px;
        }
    } //.icon-list__title

    &__sub {
        font-family: $f-light;
        font-size: 16px;

        @include respond-min(xs) {
            font-size: 18px;
            text-align: center;
        }

        @include respond-min(sm) {
            font-size: 22px;
        }
    } //.icon-list__sub

    &--white {
        color: #fff;
    } //.icon-list--white
} //.icon-list
