.screen {
    padding: 35px 0 45px;
    background-color: #fff;

    @include respond-min(md) {
        padding: 50px 0 60px;
    }

    &__title {
        position: relative;
        color: #000;
        font-family: $f;
        font-size: 25px;
        line-height: 1.3;
        margin-bottom: 20px;
        text-align: center;

        @include respond-min(xs) {
            margin-bottom: 35px;
        }

        @include respond-min(sm) {
            margin-bottom: 60px;
            line-height: 1.5;
            font-size: 35px;
        }

        &--white {
            color: #fff;
        } //.screen__title--white
    } //.screen__title

    &__desc {
        margin-bottom: 40px;
        font-family: $f;
        font-size: 18px;
        line-height: 1.7;
        color: $color-text;
        text-align: center;

        @include respond-min(md) {
            font-size: 20px;
        }

        p + p {
            margin-top: 10px;
        }
    } //.screen__description

    &__slider {
        margin-bottom: 20px;
        position: relative;



        @include respond-min(md) {
            width: 350px;
            margin: 0;
            order: 1;
            margin-right: auto;
            margin-bottom: 40px;
        }

        @include screen(768px, 1024px) {
            max-width: 70%;
            margin: auto auto 20px;
        }

        .bb-slider__slide + .bb-slider__slide {
            @include respond-min(md) {
                margin-top: 20px;
            }
        }
    } //.screen__icon-slider

    &__btn {
        display: block;
        width: 100%;
        font-size: 17px;

        @include respond-min(md) {
            width: 300px;
            margin: auto;
        }

        @include respond-min(xs) {
            width: 400px;
            margin: auto;
        }
    } //.screen__btn

    &--get-icons {

        .container {
            @include respond-min(md) {
                max-width: 900px;
            }
        }

        .screen__inner {
            margin-bottom: 20px;

            @include respond-min(md) {
                display: flex;
                align-items: center;
                margin-bottom: 60px;
            }
        }

        .list--square {
            margin-bottom: 50px;
            @include respond-min(md) {
                margin-right: 60px;
                margin-bottom: 0;
            }
        }
    } //.screen--get-icons

    &--icons {

        .screen__slider {

            @include respond-min(xs) {
                max-width: none;
                width: 100%;
            }
        }
    } //.screen--icon-collect

    &--adv {

        .text-block {

            &__wrap {
                @include respond-min(md) {
                    flex-direction: row;
                    align-items: baseline;
                }
            }
        }

        img {
            @include respond-min(md) {
                width: 80%;
                margin: auto;
            }
        }

        .screen__img {
            @include respond-min(md) {
                display: block;
                width: 100%;
            }

            img {
                width: 80%;
                margin: auto;
            }
        }
    } //.screen--advantages

    &--join-us {
        @include sectionOverlay;
        @include cover;
    } //.screen--join-us

    &--reg {
        padding: 20px 0;

        @include respond-min(md) {
            display: none;
        }
    } //.screen--reg

    &--reviews {

        .bb-slider__slide {
            @include respond-min(md) {
                padding: 0 30px;
            }
        }
    } //.screen--reviews

    &--site {
        .container {
            @include respond-min(md) {
                width: 1000px;
            }
        }

        .screen {

            &__inner {
                margin-bottom: 20px;
                align-items: center;
                @include flexColumn;

                @include respond-min(md) {
                    flex-direction: row;
                    margin-bottom: 40px;
                    @include row-flex;
                }
            }

            &__text {
                @include col;
                @include size(7);
                @include size-xs(12);
                @include flexColumn;
                align-items: center;
                justify-content: center;


                .text-block__item {
                    @include respond-min(md) {
                        margin-bottom: 25px;
                    }
                }

                .text-block__icon {
                    @include respond-min(md) {
                        width: 100px;
                        height: 100px;
                        margin-bottom: 10px;
                    }
                }

                .text-block__title {
                    @include respond-min(md) {
                        font-size: 20px;
                    }
                }

                .text-block__item {
                    text-align: center;
                    max-width: 430px;
                    @include size(12);
                }
            }

            .mockup-slider__wrapper {
                @include col;
                @include size-xs(12);
                @include size(5);
            }
        }
    }

    &.bg-grey {
        background: $color-bg--lightest;
    } //.screen.grey

    .mockup-slider {
        width: 100%;
        padding: 22% 10.2% 26% 9%;

        background-size: cover;

        .slick-slide {
            margin: 0;
        }

        .bb-slider__slide {
            box-shadow: inset 5px 5px 20px rgba(0,0,0, 0.4);
        }

        &__wrapper {
            width: 100%;

            @include respond-min(md) {
                width: 38%;
            }
        }
    }

    .form {
        width: 100%;

        @include respond-min(md) {
            color: $color-text--darkest;
            background-color: rgba(255, 255, 255, 0.75);
            border-radius: $border-radius;
            margin: 0;
            margin-left: auto;
        }

        &__title {
            font-family: $f-bold;
            font-size: 27px;
            line-height: 1.2;
            width: 260px;
        }

        .bb-checkbox__box {
            border-color: $color-border;
        }

        .bb-checkbox--radio .bb-checkbox__title {
            font-size: 14px;
        }

        .bb-checkbox:hover .bb-checkbox__box {
            border-color: $color-main;
        }

        .bb-title--devider::after,
        .bb-title--devider::before {
            border-top: 1px solid $gray--light;
        }
    } //.form
} //.screen

.text-block {
    @include row-flex;

    &__item {
        margin-bottom: 40px;
        @include flexColumn;
        align-items: center;

        @include col;
        @include size(4);
        @include size-sm(12);

        @include respond-to(sm) {
            max-width: 350px;
            margin: auto auto 40px;
        }
    }

    &__icon {
        width: 119px;
        height: 119px;
        margin-bottom: 20px;
        display: flex;

        &.h50 {
            @include respond-to(xs) {
                height: 80px;
            }
        }
    } //.text-block__icon

    &__title {
        margin-bottom: 20px;
        font-size: 20px;
        font-family: $f-medium;

        @include respond-min(md) {
            font-size: 25px;
        }
    } //.text-block__title

    &__sub {
        font-family: $f-light;
        font-size: 16px;
        line-height: 1.5;
        text-align: center;

        @include respond-min(md) {
            font-size: 17px;
        }
    } //.text-block__sub

    &--icon {
    } //.text-block--icon
} //.text-block

.video {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;

    @include respond-min(md) {
        margin-left: 0;
    }

    iframe {
        @include coverdiv;
    }
} //.screen__video


