.registration {

    .firstscreen {

        &__title {
            margin-bottom: 20px;

            @include respond-min(md) {
                margin-bottom: 30px;
            }
        } //firstscreen__title

        &__subtitle {
            margin-bottom: 50px;

            @include respond-min(xs) {
                margin-bottom: 20px;
            }
        } //firstscreen__subtitle

        &__btn {
            align-self: center;
        } //firstscreen__btn
    } //.firstscreen

    .icon-list {

        &__wrap {
            margin-bottom: 50px;

            @include respond-min(md) {
                margin-bottom: 70px;
            }
        } //icon-list__wrap
    } //.icon-list

    .video__wrapper {
        @include respond-min(md) {
            width: 1000px;
            margin: auto;
        }
    } //.registration .video__wrapper
} //.registration
