.header {
    background-color: transparent;

    &__inner {
        height: 50px;
        @include flexCenter;
    } //.header__inner

    &__logo {

        .logo__link {
            font-family: $f-medium;
            font-size: 15px;
        }
    } //.header__logo

    &.bg--dark {

        .logo {

            &__link {
                color: #fff;
            }

            &:hover {
                color: #fff;
            }
        }
    }

    .container {
        padding: 0 10px 0 0;
    } //.header .container
} //.header

.logo {
    display: inline-block;
    font-family: encode-700, sans-serif;
    font-size: 18px;
    letter-spacing: 1px;

    @include respond-to(sm) {
        margin-right: 15px;
    }

    @include respond-to(sm) {
        margin-right: 0;
        flex: 1;
    }

    @include respond-to(i5) {
        font-size: 17px;
    }

    &__link {
        color: $gray--darkest;

        &:hover {
            color: $gray--darkest;
        }
    } //.logo__link

    &__img {
        width: 130px;
    } //.logo__img

    span {
        color: $color-main;
    } //.logo span
} //.logo
